* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h2, h4 {
    text-align: center;
}

body {
    font-family: Ubuntu;
    background-color: rgb(199, 248, 255);
    color: black;
}

.infoContainer {
    height: 250px;
}

#headTitle {
    text-align: center;
    font-size: 50px;
    color: black;
}

#headTitle:hover {
    color: red;
}

#headInfo {
    text-align: center;
    font-size: 40px;
    color: black;
}

#headInfo:hover {
    color: rgb(226, 233, 0);
}

.img-container {
    text-align: center;
    width: 300px;
    height: 250px;
}

img {
    max-width: 200px;
    max-height: 250px;
}

.book {
    background-color: white;
    width: 300px;
}

.info-container:hover {
    background-color: gold;
}

.bookList {
    width: 80vw;
    max-width: 1080px;
    margin: 5px auto;
    display: grid;
    gap: 5rem;
}

.btn-container {
    text-align: center;
}

.buyBtn {
    position: relative;
    height: 40px;
    width: 160px;
    border-radius: 1rem;
    border: 3px solid;
    border-color: darkblue;
    background-color: rgba(114, 33, 244, 0.4);
}

@media screen and (min-width: 768px) {
    .bookList {
        grid-template-columns: repeat(3, 1fr);
    }
}

.end {
    height: 200px;
    text-align: center;
}

.ending {
    position: relative;
    top: 100px;
    font-size: 50px;
    font-weight: 500;
    font-family: monospace;
    letter-spacing: 5px;
    color: black;
}

.ending:hover {
    color: darkorange;
}